"use client";

import React, { ReactNode, useEffect, useState } from "react";
import { SessionProvider, useSession, signOut } from "next-auth/react";
import Link from "next/link";

function Navigation() {
  const { data: session } = useSession();
  const [role, setRole] = useState<string | null>(null);

  useEffect(() => {
    if ((session?.user as any)?.role) {
      setRole((session.user as any).role);
    }
  }, [session]);

  if (!session) {
    return (
      <nav className="bg-gray-800 p-4 text-white flex justify-between">
        <div className="font-bold">Echodoor</div>
        <div>
          <Link href="/login" className="hover:underline">
            Login
          </Link>
        </div>
      </nav>
    );
  }

  return (
    <nav className="bg-gray-800 p-4 text-white flex justify-between">
      <div className="font-bold">Echodoor</div>
      <div className="space-x-4">
        {role === "admin" && (
          <>
            <Link href="/admin/dashboard" className="hover:underline">
              Admin Dashboard
            </Link>
            <Link href="/admin/representatives" className="hover:underline">
              Representatives
            </Link>
            <Link href="/admin/clients" className="hover:underline">
              Clients
            </Link>
            <Link href="/admin/products" className="hover:underline">
              Products
            </Link>
            <Link href="/admin/orders" className="hover:underline">
              Orders
            </Link>
          </>
        )}
        {role === "representative" && (
          <>
            <Link href="/representative/dashboard" className="hover:underline">
              Dashboard
            </Link>
            <Link href="/representative/clients" className="hover:underline">
              My Clients
            </Link>
            <Link href="/representative/orders" className="hover:underline">
              My Orders
            </Link>
            <Link href="/representative/create-order" className="hover:underline">
              Create Order
            </Link>
          </>
        )}
        <button
          onClick={() => signOut()}
          className="bg-red-600 px-3 py-1 rounded hover:bg-red-700"
        >
          Logout
        </button>
      </div>
    </nav>
  );
}

export default function RootLayout({ children }: { children: ReactNode }) {
  return (
    <html lang="en">
      <head>
        <title>Echodoor Atendimento</title>
      </head>
      <body>
        <SessionProvider>
          <Navigation />
          <main className="p-4">{children}</main>
        </SessionProvider>
      </body>
    </html>
  );
}
